$accent: #227aff;
$primary: #001931;
$primary-70: #4d5e6f;
$dark: #808c98;
$danger: #FF3945;
$border-light: #DBE0E9;
$light: #EEF1F7;
$black: #000000;
$white: #ffffff;

body {
  .masterstudy-starter-wizard {
    display: flex;
    max-width: 1000px;
    flex-direction: column;
    position: relative;
    border-radius: 10px;
    background: $white;
    box-shadow: 0px 1px 3px 0px rgba($black, 0.07);
    font-family: "Albert Sans";
    margin: 0 auto;
    padding: 0;
    color: $primary;

    img {
      display: block;
      max-width: 100%;
      height: auto;
      object-fit: cover;
    }

    &__title {
      color: $primary;
      font-size: 30px;
      font-weight: 700;
      margin-bottom: 40px;
    }
    &__button-box {
      display: flex;
      justify-content: space-between;
      padding: 15px 0;
      .masterstudy-starter-wizard__button {
        &-next {
          display: none;
        }
        &-message {
            display: none;
            align-items: center;
            color: $danger;
            background-color: rgba($danger, 0.1);
            border-radius: 4px;
            padding: 10px 15px;
            font-size: 14px;
        }
      }
      &__hide {
        visibility: hidden;
        opacity: 0;
      }
      &.hide-install {
        .masterstudy-starter-wizard__button {
          &-next {
            display: block;
            margin-left: auto;
          }
          &-install,
          &-skip,
          &-install-demo {
            display: none;
          }
        }
      }
      &.has-demo-error {
        .masterstudy-starter-wizard__button {
            &-install-demo {
                display: block;
            }
            &-message {
                display: flex;
            }
            &-next,
            &-skip {
                display: none;
            }
        }
      }
    }
    &__button {
      display: block;
      position: relative;
      padding: 13px 20px;
      border-radius: 4px;
      background-color: $accent;
      text-align: center;
      text-decoration: none;
      font-size: 14px;
      font-weight: 700;
      color: $white;
      cursor: pointer;
      transition: all 0.3s;
      &-skip {
        background-color: $light;
        color: rgba($primary, 0.7);
      }
      &-close {
        display: inline-block;
        width: 25px;
        height: 25px;
        position: relative;
        background-color: $light;
        border-radius: 30px;
        cursor: pointer;
        &::before,
        &::after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: 10px;
          height: 1px;
          background-color: $primary;
          transform-origin: center;
          transform: translate(-50%, -50%) rotate(45deg);
          transition: all 0.3s;
        }
        &::after {
          transform: translate(-50%, -50%) rotate(-45deg);
        }
        &:hover {
          &::before,
          &::after {
            background-color: rgba($primary, 0.5);
          }
        }
      }
      &:active,
      &:focus {
        border: 0;
        box-shadow: none;
      }
      &-reset {
        background-color: $danger;
        &.disabled {
          opacity: 0.5;
          cursor: default;
        }
      }
    }
    &__navigation {
      display: flex;
      align-items: center;
      min-height: 50px;
      border-bottom: 1px solid $border-light;
      position: relative;
      &-back-button {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        outline: none;
        box-shadow: none;
        border-right: 1px solid $border-light;
        text-decoration: none;
        color: $dark;
        cursor: pointer;
      }
      &-progress-bar {
        margin: 0;
        padding: 0 70px;
        width: 100%;
        box-sizing: border-box;
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        li {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-wrap: wrap;
          text-align: center;
          position: relative;
          min-height: 50px;
          margin: 0 10px;
          padding: 10px 0;
          box-sizing: border-box;
          line-height: 18px;
          font-size: 14px;
          font-weight: 500;
          color: rgba($primary, 0.7);
          span {
            display: inline-block;
            vertical-align: top;
            width: 18px;
            height: 18px;
            margin: -1px 5px 0 0;
            text-align: center;
            border-radius: 50px;
            background: rgba($primary, 0.5);
            font-size: 12px;
            font-weight: 700;
            color: $white;
            em {
              font-style: normal;
            }
            i {
              font-style: normal;
              display: none;
            }
          }
          &.progress-step-done {
            span {
              background: rgba($accent, 0.1);
              em {
                display: none;
              }
              i {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100%;
                font-size: 10px;
                color: $accent;
              }
            }
          }
          &.active {
            color: $accent;
            span {
              background: $accent;
            }
            &::after {
              content: "";
              position: absolute;
              right: 0;
              bottom: -1px;
              left: 0;
              height: 2px;
              background-color: $accent;
            }
          }
        }
      }
      @media (max-width: 1023px) {
        &-back-button {
            display: none;
        }
        &-progress-bar {
            padding: 0 20px;
        }
      }
    }
    &__wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      box-sizing: border-box;
      min-height: 560px;
      padding: 40px;
      &-header {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;
        &__title {
          font-size: 30px;
          font-weight: 700;
        }
        &__price {
          padding: 9px 20px;
          border-radius: 8px;
          font-size: 15px;
          font-weight: 700;
          color: $white;
          background: url('../images/price-label.png')  no-repeat;
          background-size: cover;
        }
        &__builders {
          span {
            display: inline-block;
            vertical-align: top;
            padding: 5px 14px;
            border-radius: 40px;
            background-color: transparent;
            font-size: 14px;
            font-weight: 500;
            color: $primary-70;
            cursor: pointer;
            &.active {
              background-color: rgba($accent, 0.1);
              color: $accent;
            }
          }
        }
      }
      &-content {
        width: 100%;
        ul {
          margin: 0 -10px;
          padding: 0;
          width: 100%;
          box-sizing: border-box;
          list-style: none;
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;
          align-items: flex-start;
          li {
            width: 25%;
            position: relative;
            transition: all 0.3s;
            &.masterstudy-starter-wizard__template-gutenberg {
              display: none;
            }
            .masterstudy-starter-wizard__template {
              padding: 0 10px;
              &-image {
                position: relative;
                margin-bottom: 10px;
                border: 1px solid $border-light;
                border-radius: 4px;
                overflow: hidden;
                &::before {
                  content: "";
                  position: absolute;
                  top: 0;
                  right: 0;
                  bottom: 0;
                  left: 0;
                  border-radius: 4px;
                  border: 2px solid $accent;
                  transition: all 0.3s;
                  visibility: hidden;
                  opacity: 0;
                }
              }
              &-status {
                position: absolute;
                top: 10px;
                right: 10px;
                color: $white;
                text-align: center;
                font-size: 10px;
                font-weight: 700;
                letter-spacing: 1px;
                text-transform: uppercase;
                border-radius: 4px;
                padding: 3px 5px;
                background: $primary-70;
                &.premium {
                  background-color: $accent;
                }
              }
              &-title {
                color: $primary-70;
                text-align: center;
                font-size: 10px;
                font-weight: 700;
                letter-spacing: 1px;
                text-transform: uppercase;
                margin-bottom: 10px;
              }
              &-message {
                position: relative;
                top: -10px;
                color: rgba($primary, 0.5);
                margin-top: 10px;
                font-size: 12px;
                font-weight: 500;
                text-align: center;
                transition: all 0.3s;
                visibility: hidden;
                opacity: 0;
              }
              &-installed {
                display: none;
                align-items: center;
                justify-content: center;
                text-align: center;
                position: absolute;
                right: 0;
                bottom: 20px;
                left: 0;
                font-weight: 500;
                font-size: 14px;
                color: $white;
                span {
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  width: 20px;
                  height: 20px;
                  margin-right: 10px;
                  border-radius: 50%;
                  background-color: $accent;
                  font-size: 10px;
                }
              }
              &.installed {
                .masterstudy-starter-wizard__template {
                  &-installed {
                    display: flex;
                  }
                  &-image {
                    &::before {
                      display: block;
                      background: linear-gradient(180deg, rgba(0, 25, 49, 0) 0%, rgba(0, 25, 49, 0.70) 100%);
                      border: 0;
                      visibility: visible;
                      opacity: 1;
                    }
                  }
                  &-status,
                  &-message {
                    display: none;
                  }
                }
                .masterstudy-starter-wizard__button {
                  display: none;
                }
              }
            }

            .masterstudy-starter-wizard {
              &__button {
                top: -10px;
                visibility: hidden;
                opacity: 0;

                &-preview {
                  position: absolute;
                  top: auto;
                  right: 10px;
                  bottom: 10px;
                  left: 10px;
                  margin: 0 0 -60px 0;
                  background-color: $primary-70;
                  &:hover {
                    background-color: rgba($accent, 0.9);
                  }
                }
              }
            }

            &:hover {
              .masterstudy-starter-wizard__template {
                &-image {
                  &::before {
                    visibility: visible;
                    opacity: 1;
                  }
                }
                &-message {
                  position: relative;
                  top: 0;
                  visibility: visible;
                  opacity: 1;
                  transition-delay: 0.1s;
                }
              }
              .masterstudy-starter-wizard {
                &__button-preview,
                &__button {
                  position: relative;
                  top: 0;
                  visibility: visible;
                  opacity: 1;
                  &-preview {
                    position: absolute;
                    top: auto;
                    margin-bottom: 0;
                  }
                }
              }
            }
            @media (max-width: 991px) {
              width: 33.333%;
              margin-bottom: 20px;
            }
          }
        }
      }
    }
    &__template-popup {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      align-items: center;
      justify-content: center;
      background-color: rgba($black, 0.4);
      z-index: 9999;
      &.reinstallation {
        display: flex !important;
      }
      &__content {
        background-color: $white;
        max-width: 500px;
        margin: 0 auto;
        padding: 30px;
        border-radius: 10px;
        position: relative;
        box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.25);
        h2 {
          padding-left: 30px;
          margin: 0 0 10px;
          padding: 0 30px 0 0;
          line-height: 28px;
          font-size: 24px;
          font-weight: 700;
        }
        p {
          font-size: 14px;
          font-weight: 500;
          margin: 0 0 30px;
        }
        .masterstudy-starter-wizard {
          &__button {
            &-box {
              padding: 30px 0 0;
              justify-content: flex-start;
            }
            &-skip {
              display: block !important;
              margin-right: 10px;
            }
            &-close {
              position: absolute;
              top: 32px;
              right: 30px;
            }
          }
        }
      }
    }
    &__progress {
      &-wrap {
        width: 100%;
        height: 6px;
        border-radius: 10px;
        overflow: hidden;
        margin-top: 20px;
        display: none;
      }
      &-bar {
        width: 100%;
        height: 100%;
        position: relative;
        &-fill {
          width: 0;
          height: 100%;
          background-color: $accent;
          transition: width 2s ease;
        }
      }
    }
    &__plugins,
    &__demos,
    &__child-theme {
      max-width: 550px;
      text-align: center;
      margin: 0 auto;
      ul {
        flex-direction: column;
        margin: 0;
        padding: 0;
        gap: 0;
        .masterstudy-starter-wizard__plugin,
        .masterstudy-starter-wizard__demo,
        .masterstudy-starter-wizard__child-theme__icon-box {
          display: flex;
          flex-wrap: wrap;
          list-style: none;
          width: 100%;
          max-width: 100%;
          position: relative;
          margin: 0;
          padding: 15px 0;
          border-bottom: 1px solid $border-light;
          &-image {
            display: block;
            max-width: 50px;
            max-height: 50px;
            margin-right: 15px;
          }
          &-info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: left;
            &__title {
              font-size: 18px;
              font-weight: 700;
            }
            &__description {
              font-size: 14px;
              font-weight: 500;
              color: rgba($primary, 0.7);
            }
          }
          &-indicator {
            position: absolute;
            top: 50%;
            right: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            margin-top: -15px;
            border: 2px solid $accent;
            animation: spin 1s linear infinite;
            border-top-color: transparent;
            visibility: hidden;
            opacity: 0;
          }
          &-load {
            .masterstudy-starter-wizard__plugin-indicator,
            .masterstudy-starter-wizard__demo-indicator {
              visibility: visible;
              opacity: 1;
              span {
                display: none;
              }
            }
          }
          &-loaded {
            .masterstudy-starter-wizard__plugin-indicator,
            .masterstudy-starter-wizard__demo-indicator {
              color: $accent;
              background-color: rgba($accent, 0.1);
              border: 0;
              animation: none;
              visibility: visible;
              opacity: 1;
              span {
                display: block;
              }
            }
          }
          &-error {
            .masterstudy-starter-wizard__plugin-indicator,
            .masterstudy-starter-wizard__demo-indicator {
              color: $danger;
              background-color: rgba($danger, 0.1);
              border: 0;
              animation: none;
              visibility: visible;
              opacity: 1;
              span {
                display: block;
                &::before,
                &::after {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    width: 14px;
                    height: 2px;
                    background-color: $danger;
                    transform-origin: center;
                    transform: translate(-50%, -50%) rotate(45deg);
                }
                &::after {
                    transform: translate(-50%, -50%) rotate(-45deg);
                }
              }
            }
          }
        }
      }
    }
    &__demos {
      ul {
        .masterstudy-starter-wizard__demo {
          &.disable-check {
            .masterstudy-starter-wizard__demo {
                &-checkbox {
                    label {
                        cursor: default;
                    }
                    span {
                        opacity: 0.5;
                    }
                }
            }
          }
        }
      }
      .masterstudy-starter-wizard__button-box {
        justify-content: flex-end;
      }
    }
    &__demo-checkbox {
      label {
        display: flex;
        align-items: center;
        line-height: 18px;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
      }
      .demo-checkbox {
        display: inline-block;
        vertical-align: top;
        box-sizing: border-box;
        width: 20px;
        height: 20px;
        line-height: 18px;
        margin-right: 10px;
        border-radius: 4px;
        text-align: center;
        border: 2px solid $accent;
        background-color: $accent;
        font-size: 12px;
        color: $white;
        span {
          line-height: 16px;
        }
        &[data-checked="false"] {
          background-color: transparent;
          border-color: $border-light;
        }
      }
    }
    &__child-theme {
      ul {
        .masterstudy-starter-wizard__child-theme__icon-box {
          display: flex;
          flex-wrap: nowrap;
          max-width: 100%;
          padding: 10px 0;
          border: 0;
          &__icon {
            display: flex;
            width: 40px;
            height: 40px;
            min-width: 40px;
            min-height: 40px;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border-radius: 8px;
            margin-right: 20px;
            background: rgba($accent, 0.1);
            font-size: 22px;
            color: $accent;
          }
          &__info {
            text-align: left;
            &-title {
              color: $primary;
              font-size: 16px;
              font-weight: 700;
            }
            &-description {
              color: rgba($primary, 0.7);
              font-size: 14px;
              font-weight: 500;
            }
          }
        }
      }
    }
    &__finish {
      max-width: 550px;
      text-align: center;
      margin: 0 auto;
      .masterstudy-starter-wizard {
        &__icon {
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          width: 80px;
          height: 80px;
          margin: 0 auto;
          border-radius: 50%;
          background-color: rgba($accent, 0.1);
          font-size: 24px;
          color: $accent;
          &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            margin: -25px 0 0 -25px;
            background-color: rgba($accent, 0.1);
          }
        }
        &__title {
          color: $primary;
          font-size: 36px;
          font-weight: 700;
          line-height: 36px;
          margin: 20px 0;
        }
        &__description {
          color: $primary-70;
          text-align: center;
          font-size: 16px;
          font-weight: 500;
          line-height: 24px;
          margin-bottom: 25px;
        }
        &__button-box {
          justify-content: center;
          align-items: center;
          .masterstudy-starter-wizard__button {
            color: $white;
            &:hover {
              text-decoration: none;
            }
          }
          a {
            margin: 0 10px;
            display: inline;
            color: $primary-70;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
}
